@import "../../../theme/variables.less";

@grid-nav-height: 55px;

.stamp-content {
    height: calc(~"100% - 110px");
    position: relative;
    top: @grid-nav-height;
    .row {
        height: calc(~"100% - 60px"); // @grid-nav-height;
        //overflow-y: auto; // remove if we want independent scrollers
    }
}

.input-group.quick-search {
    input.form-control {
        width: auto;
    }
}

collapse-panel {
    .panel {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }
}

.stamp-grid-nav {
	top: @grid-nav-height;
	height: @grid-nav-height;
	margin-bottom: 10px;
	border-radius: 0;
	font-size: 0.9em;
	.form-control {
		height: 30px;
		font-size: 0.9em;
	}
	.btn-toolbar {
        width: 100%;
		position: absolute;
		top: 50%;
		transform: translate(0,-50%);
	}
	.btn {
		&.disabled {
			opacity: 0.35;
		}
	}
}

.stamp-list-footer {
	bottom: 0px;
	position: relative;
	width: 100%;
	border-radius: 0;
	border-top: ridge 1px #efefef;
	margin: 5px 0 0 0;
	font-size: 0.9em;
	.form-control {
		height: 30px;
	}
	.pagination {
		display: block;
		margin: 0;
	}
    .report-statistics {
        float:right;
        margin-right: 25px;
        display: inline-block;
        .statistics-button {
            padding: @padding-xs-horizontal / 2;
            color: @pagination-color;
            background-color: @pagination-bg;
            border-color: @pagination-border;
            &.active {
                background-color: @pagination-disabled-bg;
            }
            &:focus {
                outline: none;
            }

            &:hover {
                color: @pagination-active-color;
                background-color: @pagination-active-bg;
                border-color: @pagination-active-border;
                &:disabled {
                    color: @pagination-disabled-color;
                }
            }
        }
        .value-display {
            border: 1px solid @btn-default-border;
            border-radius: 0 @border-radius-small @border-radius-small 0;
            width: 100px;
            height: 25px;
            left: -1px;
            position: relative;
            background-color: @navbar-default-bg;
            padding: 0 5px;
            font-size: @font-size-small;
        }
    }
    .btn-toolbar {
        .badge {
            position: relative;
            top: -2px;
            font-weight: normal;
        }
        float: right;
    }
}

.sort-actions {
    margin-right: 5px;
    .dropdown {
        float: left;
    }
    .sort-selector {
        width: 150px;
        text-align: left;
        float: left;
        border-radius: @border-radius-base 0 0 @border-radius-base;
        .caret {
            float: right;
            top: 7px;
            position: relative;
        }
    }
}


.result-size-selector {
	width: 65px;
	text-align: left;
	.caret {
		float: right;
		top: 7px;
		position: relative;
	}
}

.results-group {
	float: right;
	line-height: 30px;
	position: relative;
}

.selection-group {
    margin-right: 15px;
    line-height: 30px;
    position: relative;
    float: right;
}

.sw-editor-container {
	height: 100%;
	display: flex;
	display: -webkit-flex;
	flex-direction: row;
	-webkit-flex-direction: row;
	-webkit-align-content: stretch;
	align-content: stretch;
	position: relative;
    padding: 0;
}

.sw-dialog-wrapper {
	.modal-body {
		.bootbox-body {
			text-overflow: ellipsis;
			white-space: nowrap;
			overflow: hidden;
		}
	}
}
